@import "../../../scss/settings/colors";
@import "../../../scss/settings/mixins";

.summary__address {
  padding: 0 1rem 0 1rem;
  @include mediumScreen {
    padding-top: 1rem;
    max-width: 450px;
    margin-right: 2rem;
  }
  .summary__address-subtitle {
    font-size: 1.2rem;
    font-weight: 700;
    padding: 0 0 1rem 0;
    @include mediumScreen {
      font-size: 1.5rem;
      padding: 0 0 2rem 0;
    }
  }
  .summary__address-content {
    .summary__details {
      padding-bottom: 1rem;
      display: flex;
      align-items: center;
      @include mediumScreen {
        padding-bottom: 2rem;
      }
      .summary__details-name {
        white-space: pre-line;
        font-size: 1.1rem;
        width: 100px;
        @include mediumScreen {
          font-size: 1.5rem;
          width: 130px;
        }
      }
      .summary__details-value {
        font-size: 1.1rem;
        @include mediumScreen {
          font-size: 1.5rem;
        }
      }
    }
  }
}
